import store from '../../store/index';
export default {
    install(Vue) {
        // 获取微信分享参数
        Vue.prototype.$shareConfig = (
            sharetitle,
            sharedesc,
            shareimg,
            shareurl
        ) => {
            //设置参数默认值
            if (!sharetitle) {
                sharetitle = "点击进入聚拍 action";
            }
            if (!sharedesc) {
                sharedesc = "聚拍-提升剧组协作效率的实用工具";
            }
            if (!shareimg) {
                shareimg =
                    "https://jumijupai.oss-cn-beijing.aliyuncs.com/jupai/1534988711050.png";
            }
            if (!shareurl) {
                shareurl = location.origin + "/views/h5/playGroup";
            }

            wx.config({
                appId: Vue.$global.appId,//公众号的唯一标识
                debug: Vue.$global.debug,//是否打开调试模式
                jsApiList: Vue.$global.jsApiList,//需要使用的js接口列表
                nonceStr: Vue.$global.nonceStr,//生成签名的随机串
                signature: Vue.$global.signature,//签名
                timestamp: Vue.$global.timestamp//生成签名的时间戳
            });
            wx.ready(function () {
                wx.onMenuShareTimeline({
                    title: sharetitle,
                    desc: sharedesc,
                    link: shareurl,
                    imgUrl: shareimg,
                    success: function () { },
                    cancel: function () { }
                });
                wx.onMenuShareQQ({
                    title: sharetitle,
                    desc: sharedesc,
                    link: shareurl,
                    imgUrl: shareimg,
                    success: function () { },
                    cancel: function () { }
                });
                wx.onMenuShareAppMessage({
                    title: sharetitle,
                    desc: sharedesc,
                    link: shareurl,
                    imgUrl: shareimg,
                    success: function () { },
                    cancel: function () { }
                });
                wx.onMenuShareWeibo({
                    title: sharetitle,
                    desc: sharedesc,
                    link: shareurl,
                    imgUrl: shareimg,
                    success: function () { },
                    cancel: function () { }
                });
            });
        };
        //调起微信支付接口
        Vue.prototype.$payConfig = (
            timestamp = 0, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
            nonceStr = '', // 支付签名随机串，不长于 32 位
            apackage = '', // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
            signType = '', // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
            paySign = '', // 支付签名
            success
        ) => {
            console.log(timestamp)
            console.log(nonceStr)
            console.log(apackage)
            console.log(signType)
            console.log(paySign)
            console.log(success)
            console.log(213)
            console.log(store)
            wx.config({
                debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                appId: store.state.WXConfig.appId, // 必填，公众号的唯一标识
                timestamp: store.state.WXConfig.timestamp, // 必填，生成签名的时间戳
                nonceStr: store.state.WXConfig.nonceStr, // 必填，生成签名的随机串
                signature: store.state.WXConfig.signature,// 必填，签名
                jsApiList: ['chooseWXPay'] // 必填，需要使用的JS接口列表
            })
            wx.ready(function () {
                wx.chooseWXPay({
                    timestamp: timestamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
                    nonceStr: nonceStr, // 支付签名随机串，不长于 32 位
                    package: apackage, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
                    signType: signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
                    paySign: paySign, // 支付签名
                    success: success
                });
            })

        }
    }
};
